@import url(https://fonts.googleapis.com/css?family=Alegreya+Sans+SC:100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 200vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'Major Mono Display';
  font-weight: 5;
  src: local('Major Mono Display'), url(/luketpena.github.io/static/media/MajorMonoDisplay-Regular.c04d1551.ttf) format('truetype');
}

:root {
  --font-title: 'Major Mono Display', sans-serif;
  --font-header: 'Alegreya Sans SC', sans-serif;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: rgba(25,29,50,1);
}

.highlight {
  color: #FFC145;
}

.anchor {
  display:block;
  padding-top: 48px;
  margin-top: -48px;
  height: 0;
}

/* width */
::-webkit-scrollbar {
  width: 0px;
}

/* Track */
::-webkit-scrollbar-track {
  background: none;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
