@import url('https://fonts.googleapis.com/css?family=Alegreya+Sans+SC:100&display=swap');
@font-face {
  font-family: 'Major Mono Display';
  font-weight: 5;
  src: local('Major Mono Display'), url('../../fonts/MajorMonoDisplay-Regular.ttf') format('truetype');
}

:root {
  --font-title: 'Major Mono Display', sans-serif;
  --font-header: 'Alegreya Sans SC', sans-serif;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: rgba(25,29,50,1);
}

.highlight {
  color: #FFC145;
}

.anchor {
  display:block;
  padding-top: 48px;
  margin-top: -48px;
  height: 0;
}

/* width */
::-webkit-scrollbar {
  width: 0px;
}

/* Track */
::-webkit-scrollbar-track {
  background: none;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}